:root {
  --bg-color: #12171b;
  --bg-button: #1d86c7;
  --bg-button-hover: #1b71a6;
  --bg-button-active: #0c4264;
  --text-color-light: #a2b0be;
  --text-color-dark: #2b425a;
  --anim-sm: .2s;
  --anim-md: .3s;
  --anim-lg: .5s;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@keyframes slide-in {
  0% {
    transform: translate3d(min(-90%, -30rem), 0, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slide-out {
  0% {
    transform: translate3d(0, 0, 0);
  }

  100% {
    transform: translate3d(min(-90%, -30rem), 0, 0);
  }
}

::backdrop {
  opacity: .75;
  background-image: linear-gradient(90deg, #0a221d, #153320);
}

dialog {
  background-color: var(--bg-color);
  border: none;
  flex-direction: column;
  width: min(90%, 30rem);
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate3d(min(-90%, -30rem), 0, 0);
}

dialog[open] {
  animation: slide-in var(--anim-sm) forwards;
}

dialog[close] {
  animation: slide-out var(--anim-sm) forwards;
}

.dialog-wrapper {
  color: var(--text-color-light);
  flex-grow: 1;
  height: -moz-fit-content;
  height: fit-content;
  padding: 1rem 2rem;
  font-size: medium;
  position: relative;
  overflow: hidden;
}

.dialog-wrapper h1 {
  border-bottom: 1px solid var(--text-color-light);
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 1rem;
  padding-bottom: .5rem;
  padding-right: 1rem;
  font-size: medium;
}

.dialog-wrapper p {
  margin-bottom: .5rem;
  line-height: 1.4;
}

.btn-dialog-close {
  z-index: 2;
  -webkit-user-select: none;
  user-select: none;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  padding: 0;
  font-size: large;
  position: absolute;
  top: .5rem;
  right: 1rem;
}

a.link-repo:link, a.link-repo:visited {
  color: var(--bg-button);
  margin: 1rem 0 0;
  font-size: small;
  display: inline-block;
}

a.link-repo:hover, a.link-repo:focus {
  color: var(--bg-button-hover);
}

html {
  height: 100%;
}

body {
  background: var(--bg-color);
  color: var(--text-color-light);
  flex-direction: column;
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

main {
  flex-grow: 1;
}

footer {
  text-align: center;
  border-top: 1px solid #000;
  padding: 1rem;
  font-size: small;
}

a:link, a:visited {
  color: var(--text-color-light);
  border-bottom: 1px dotted var(--text-color-light);
  transition: var(--anim-lg);
  text-decoration: none;
}

a:visited {
  color: pink;
}

a:hover, a:focus {
  color: #fff;
}

button {
  background-color: var(--bg-button);
  color: #fff;
  cursor: pointer;
  transition: all var(--anim-sm);
  border: 0;
  border-radius: 1rem;
  margin: 0;
  padding: .5rem 1rem;
  font-size: small;
  line-height: 1;
}

button:hover, button:focus {
  background-color: var(--bg-button-hover);
}

button:active {
  background-color: var(--bg-button-active);
}

ul {
  list-style: none;
}

:not(.body__fullscreen) > header > h1 {
  text-align: center;
  margin: 1rem auto 2rem;
  padding-bottom: 1rem;
  position: relative;
}

:not(.body__fullscreen) > header > h1:before {
  content: "";
  background: var(--text-color-light);
  width: min(50%, 10rem);
  height: 1px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.body__fullscreen {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  position: fixed;
  overflow: hidden;
}

.body__fullscreen > header {
  z-index: 10;
  width: 100%;
  margin: 1rem;
  position: fixed;
  top: 0;
  left: 0;
}

.body__fullscreen > header > h1 {
  text-align: start;
  margin: 0 0 1rem;
}

.body__fullscreen > header > h1 span {
  color: #fff;
  font-size: large;
  display: block;
}

.body__fullscreen > header > h1 span + span {
  color: var(--text-color-light);
  font-size: small;
}

.body__fullscreen > header > h1:before {
  width: min(50%, 20rem);
  left: 0;
  transform: none;
}

.body__fullscreen > main {
  width: 100%;
  height: 100%;
}

.body__fullscreen > footer {
  background-color: #0006;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}

#gui {
  position: fixed;
  bottom: 4rem;
  right: 0;
  z-index: 100 !important;
}

.main-index {
  max-width: 90%;
  margin: auto;
  list-style: decimal-leading-zero inside;
}

@media only screen and (width >= 640px) {
  .main-index {
    max-width: 50%;
  }
}

.main-index li {
  text-align: center;
  margin-bottom: 1rem;
}

.tic-tac-toe__score, .cross-bord__score {
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  display: flex;
  position: absolute;
  left: 0;
}

.cross-bord__score {
  bottom: 90px;
}

